body {
  margin: 0;
}

.stopwatch {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.watch {
  border: 1vmin dotted #000;
  border-radius: 100%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.arrow-min {
  transform-origin: bottom;
  background-color: #0700ff;
  justify-content: center;
  align-items: center;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s linear infinite arrows;
  display: flex;
  position: absolute;
  bottom: 50%;
}

.arrow-sec {
  transform-origin: bottom;
  z-index: 1;
  background-color: #2c8000;
  justify-content: center;
  align-items: center;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite arrows;
  display: flex;
  position: absolute;
  bottom: 50%;
}

.arrow-min--speed-up {
  animation: 600s linear infinite arrows;
}

.arrow-sec--speed-up {
  animation: 10s linear infinite arrows;
}

.dot {
  z-index: 1;
  content: "";
  background-color: #f6a603;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 5vmin;
  height: 5vmin;
  display: flex;
  position: absolute;
}

@keyframes arrows {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
